.minted-filter-section {
	width: 100%;
	background-color: red;
	display: flex;
	justify-content: center;
	align-items: center;
}

.minted-filter-section-pages {
	width: 400px;
	height: 50px;
	margin: 20px;
	border: 2px solid black;
	border-radius: 10px;
	background-color: white;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.minted-filter-section-pages-numberedPages {
	width: 180px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.minted-filter-section-filterButton {
	width: 120px;
	height: 40px;
	margin: 20px;
	border: 4px solid black;
	border-radius: 10px;
	font-size: 2rem;
	font-weight: 500;
	background-color: yellow;
	display: flex;
	justify-content: center;
	align-items: center;
}

.minted-nft-section {
	width: 100%;
	background-color: blue;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;

}

.card {
	width: 150px;
	height: 200px;
	margin: 40px;
	background-color: pink;
}

@media (max-width: 650px) {

	.minted-filter-section {
		flex-direction: column;	
	}
}
