:root {
	font-family: 'Mitr', sans-serif;
	font-size: 16px;
	--body-background-color: rgba(255,242,126, .95);
	--header-background-color: #1E2328;
	--text-color: #FED053;
	--text-color-hover: #f1a20d;
	--twitter-discord-background-color: rgb(254,235,71);
	--twitter-background-color-hover: #f1a20d;
	--discord-background-color-hover: #f1a20d;
	--connect-background-color: rgb(254,235,71);
	--connect-background-color-hover: rgba(0, 255, 0, 0.3);
}

* {
	padding: 0;
	margin: 0;
}

body {
	background-color: rgba(255,242,126, .95);
	background-color: var(--body-background-color);
}

/* Sections */

.home-section, .minted-section, .my-section, .attributes-section, .faq-section {
	width: 100%;
	margin: 80px 0 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

/* Header */

.header {
  width: 100%;
  height: 80px;
  background-color: var(--header-background-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
}

/* Header - logo section */

.logo {
  width: 110px;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-img {
  width: 52px;
  height: 52px;
  border-radius: 100%;
}

.logo-symbol {
  font-size: 1.4rem;
  color: var(--text-color);
}

/* Header - nav section */

.nav {
  width: 700px;
  transition: top 500ms ease-in-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav.open {
  top: 80px;
}

.nav-item {
  font-size: 1.05rem;
  font-weight: 900;
  color: var(--text-color);
  text-decoration: none;
  cursor: pointer;
  transition: color, font-size 350ms ease-in-out;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.nav-item-active, .nav-item:hover {
  color: var(--text-color-hover);
  font-size: 1.25rem;
}

.nav-item-links {
  display: none;
}

.nav-item-placeholder {
  font-size: 1.25rem;
  visibility: hidden;
}

/* Header - links section */

.links {
  width: 230px;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.links-img-div {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  background-color: var(--twitter-discord-background-color);
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.links-img-div-twitter:hover {
  background-color: var(--twitter-background-color-hover);
}

.links-img-div-discord:hover {
  background-color: var(--discord-background-color-hover);
}

.twitter-img {
  width: 20px;
  height: 20px;
}

.discord-img {
  width: 25px;
  height: 25px;
}

.links-connect {
  width: 120px;
  height: 40px;
  border: 2px solid black;
  border-radius: 8px;
  background-color: var(--connect-background-color);
  cursor: pointer;
  transition: border-color, background-color 200ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.links-connect:hover {
  background-color: var(--text-color-hover);
  border-color: white;
}

.links-connect-div {
  width: 100px;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.wallet-img {
  width: 22px;
  height: 22px;
}

.connect-text {
  font-size: 1.1rem;
  font-weight: 300;
  color: var(--header-background-color);
}

.links-nav {
  display: none;
}

/* Header - Mobile menu */

.mobile-menu {
  width: 40px;
  height: 30px;
  margin: 0 20px;
  position: relative;
  display: none;
  cursor: pointer;
}

.mobile-menu span {
  width: 100%;
  height: 6px;
  display: block;
  position: absolute;
  left: 0;
  border-radius: 9px;
  background-color: var(--text-color);
  transition: .25s ease-in-out;
}

.mobile-menu span:nth-child(1) {
  top: 0px;
}

.mobile-menu span:nth-child(2), .mobile-menu span:nth-child(3) {
  top: 12px;
}

.mobile-menu span:nth-child(4) {
  top: 24px;
}

.mobile-menu.open span:nth-child(1), .mobile-menu.open span:nth-child(4) {
  width: 50%;
  top: 12px;
  left: 25%;
  opacity: 0;
}

.mobile-menu.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.mobile-menu.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

/* Header - header-placeholder */

.header-placeholder {
  display: none;
}

@media (max-width: 1235px) {
/* and (not any-hover) */

  .header {
    justify-content: center;
    align-items: center;
    position: fixed;
  }

  .header-placeholder {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: var(--header-background-color);
    z-index: 2;
  }

  .logo {
    width: 130px;
    z-index: 3;
  }

  .nav {
    width: 100%;
    height: 530px;
    position: fixed;
    top: -530px;
    left: 0;
    background-color: var(--header-background-color);
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
  }

  .nav-item {
    width: 100%;
    font-size: 1.15rem;
    cursor: default;
  }

  .nav-item:hover {
    background-color: var(--text-color);
    font-size: 1.15rem;
    color: var(--header-background-color);
  }

	.nav-item-active {
    font-size: 1.15rem;
  }

  .nav-item-placeholder {
    font-size: 1.15rem;
    /*visibility: visible;*/
  }

  .nav-item-links {
    display: flex;
  }

	.nav-item-links:hover {
		background-color: var(--header-background-color);
	}

  .div-nav-item-links {
    width: 90px;
    margin: 30px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .links-img-div {
    cursor: default;
  }

	
  .links-img-div:hover {
    background-color: var(--twitter-discord-background-color);
  }
  

  .links {
    display: none;
  }

  .mobile-menu {
    display: block;
    cursor: default;
    z-index: 4;
  }
}

@media (max-width: 1235px) and (max-height: 630px) {

  .nav {
    height: 365px;
    top: -365px;
  }

  .nav-item, .nav-item:hover, .nav-item-placeholder {
    font-size: 0.8rem;
  }

  .div-nav-item-links {
    margin: 15px 0;
  }
}


.description {
  width: 100%;
  padding: 40px 0 100px;
  background-color: var(--body-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.description-text {
  width: min(70%, 670px);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.bigText-img {
  width: 100%;
}

.description-text-small {
  padding-top: 40px;
  text-align: center;
}

.description-gif-mint {
  width: min(30%, 280px);
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.gif-img {
  box-sizing: border-box;
  width: 100%;
  border: 2px solid black;
  border-radius: 10px;
}

.description-mint-button {
  width: 90%;
  height: 4rem;
  margin-top: 20px;
  border: 4px solid black;
  border-radius: 20px;
  font-size: min(5vw, 3rem);
  font-weight: 600;
  background-color: var(--text-color-hover);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Home - mint */

.mint {
  width: 100%;
  padding: 100px 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mint-text {
  width: 90%;
  min-width: 280px;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.mint-text-h1 {
  text-align: center;
  padding-bottom: 20px;
}

.mint-text-p {
  text-align: center;
}

.mint-packages {
  display: flex;
  /*flex-direction: column;*/
  justify-content: space-between;
  align-items: center;
}

.packages-description {
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.packageSingle {
  width: 320px;
  height: 320px;
}

@media (max-width: 750px) {

  .description {
    flex-direction: column;
  }

  .description-text {
    width: 90%;
  }

  .description-gif-mint {
    width: 284px;
  }

  .gif-img {
    width: 284px;
  }

  .description-mint-button {
    font-size: 3rem;
  }
}

/* mobile version for home/minting section */
@media (max-width: 1080px) {

  .mint-packages {
    flex-direction: column;
  }
}


.minted-filter-section {
	width: 100%;
	background-color: red;
	display: flex;
	justify-content: center;
	align-items: center;
}

.minted-filter-section-pages {
	width: 400px;
	height: 50px;
	margin: 20px;
	border: 2px solid black;
	border-radius: 10px;
	background-color: white;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.minted-filter-section-pages-numberedPages {
	width: 180px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.minted-filter-section-filterButton {
	width: 120px;
	height: 40px;
	margin: 20px;
	border: 4px solid black;
	border-radius: 10px;
	font-size: 2rem;
	font-weight: 500;
	background-color: yellow;
	display: flex;
	justify-content: center;
	align-items: center;
}

.minted-nft-section {
	width: 100%;
	background-color: blue;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;

}

.card {
	width: 150px;
	height: 200px;
	margin: 40px;
	background-color: pink;
}

@media (max-width: 650px) {

	.minted-filter-section {
		flex-direction: column;	
	}
}

/* Footer */

.footer {
  width: 100%;
  height: 250px;
  background-color: var(--header-background-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-twitter-discord-div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.footer-twitter-discord-tag {
  padding-left: 20px;
  font-size: 1rem;
  color: white;
}

.footer-text {
  width: 90%;
  padding-bottom: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  text-align: center;
} 

.footer-logos-div {
  height: 100px;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 750px) {

  .footer-twitter-discord-div {
    cursor: default;
  }
}

