.description {
  width: 100%;
  padding: 40px 0 100px;
  background-color: var(--body-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.description-text {
  width: min(70%, 670px);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.bigText-img {
  width: 100%;
}

.description-text-small {
  padding-top: 40px;
  text-align: center;
}

.description-gif-mint {
  width: min(30%, 280px);
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.gif-img {
  box-sizing: border-box;
  width: 100%;
  border: 2px solid black;
  border-radius: 10px;
}

.description-mint-button {
  width: 90%;
  height: 4rem;
  margin-top: 20px;
  border: 4px solid black;
  border-radius: 20px;
  font-size: min(5vw, 3rem);
  font-weight: 600;
  background-color: var(--text-color-hover);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Home - mint */

.mint {
  width: 100%;
  padding: 100px 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mint-text {
  width: 90%;
  min-width: 280px;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.mint-text-h1 {
  text-align: center;
  padding-bottom: 20px;
}

.mint-text-p {
  text-align: center;
}

.mint-packages {
  display: flex;
  /*flex-direction: column;*/
  justify-content: space-between;
  align-items: center;
}

.packages-description {
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.packageSingle {
  width: 320px;
  height: 320px;
}

@media (max-width: 750px) {

  .description {
    flex-direction: column;
  }

  .description-text {
    width: 90%;
  }

  .description-gif-mint {
    width: 284px;
  }

  .gif-img {
    width: 284px;
  }

  .description-mint-button {
    font-size: 3rem;
  }
}

/* mobile version for home/minting section */
@media (max-width: 1080px) {

  .mint-packages {
    flex-direction: column;
  }
}

