/* Header */

.header {
  width: 100%;
  height: 80px;
  background-color: var(--header-background-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
}

/* Header - logo section */

.logo {
  width: 110px;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-img {
  width: 52px;
  height: 52px;
  border-radius: 100%;
}

.logo-symbol {
  font-size: 1.4rem;
  color: var(--text-color);
}

/* Header - nav section */

.nav {
  width: 700px;
  -webkit-transition: top 500ms ease-in-out;
  -moz-transition: top 500ms ease-in-out;
  -o-transition: top 500ms ease-in-out;
  transition: top 500ms ease-in-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav.open {
  top: 80px;
}

.nav-item {
  font-size: 1.05rem;
  font-weight: 900;
  color: var(--text-color);
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: color, font-size 350ms ease-in-out;
  -moz-transition: color, font-size 350ms ease-in-out;
  -o-transition: color, font-size 350ms ease-in-out;
  transition: color, font-size 350ms ease-in-out;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.nav-item-active, .nav-item:hover {
  color: var(--text-color-hover);
  font-size: 1.25rem;
}

.nav-item-links {
  display: none;
}

.nav-item-placeholder {
  font-size: 1.25rem;
  visibility: hidden;
}

/* Header - links section */

.links {
  width: 230px;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.links-img-div {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  background-color: var(--twitter-discord-background-color);
  cursor: pointer;
  -webkit-transition: background-color 200ms ease-in-out;
  -moz-transition: background-color 200ms ease-in-out;
  -o-transition: background-color 200ms ease-in-out;
  transition: background-color 200ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.links-img-div-twitter:hover {
  background-color: var(--twitter-background-color-hover);
}

.links-img-div-discord:hover {
  background-color: var(--discord-background-color-hover);
}

.twitter-img {
  width: 20px;
  height: 20px;
}

.discord-img {
  width: 25px;
  height: 25px;
}

.links-connect {
  width: 120px;
  height: 40px;
  border: 2px solid black;
  border-radius: 8px;
  background-color: var(--connect-background-color);
  cursor: pointer;
  -webkit-transition: border-color, background-color 200ms ease-in-out;
  -moz-transition: border-color, background-color 200ms ease-in-out;
  -o-transition: border-color, background-color 200ms ease-in-out;
  transition: border-color, background-color 200ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.links-connect:hover {
  background-color: var(--text-color-hover);
  border-color: white;
}

.links-connect-div {
  width: 100px;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.wallet-img {
  width: 22px;
  height: 22px;
}

.connect-text {
  font-size: 1.1rem;
  font-weight: 300;
  color: var(--header-background-color);
}

.links-nav {
  display: none;
}

/* Header - Mobile menu */

.mobile-menu {
  width: 40px;
  height: 30px;
  margin: 0 20px;
  position: relative;
  display: none;
  cursor: pointer;
}

.mobile-menu span {
  width: 100%;
  height: 6px;
  display: block;
  position: absolute;
  left: 0;
  border-radius: 9px;
  background-color: var(--text-color);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.mobile-menu span:nth-child(1) {
  top: 0px;
}

.mobile-menu span:nth-child(2), .mobile-menu span:nth-child(3) {
  top: 12px;
}

.mobile-menu span:nth-child(4) {
  top: 24px;
}

.mobile-menu.open span:nth-child(1), .mobile-menu.open span:nth-child(4) {
  width: 50%;
  top: 12px;
  left: 25%;
  opacity: 0;
}

.mobile-menu.open span:nth-child(2) {
  transform: rotate(45deg);
}

.mobile-menu.open span:nth-child(3) {
  transform: rotate(-45deg);
}

/* Header - header-placeholder */

.header-placeholder {
  display: none;
}

@media (max-width: 1235px) {
/* and (not any-hover) */

  .header {
    justify-content: center;
    align-items: center;
    position: fixed;
  }

  .header-placeholder {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: var(--header-background-color);
    z-index: 2;
  }

  .logo {
    width: 130px;
    z-index: 3;
  }

  .nav {
    width: 100%;
    height: 530px;
    position: fixed;
    top: -530px;
    left: 0;
    background-color: var(--header-background-color);
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
  }

  .nav-item {
    width: 100%;
    font-size: 1.15rem;
    cursor: default;
  }

  .nav-item:hover {
    background-color: var(--text-color);
    font-size: 1.15rem;
    color: var(--header-background-color);
  }

	.nav-item-active {
    font-size: 1.15rem;
  }

  .nav-item-placeholder {
    font-size: 1.15rem;
    /*visibility: visible;*/
  }

  .nav-item-links {
    display: flex;
  }

	.nav-item-links:hover {
		background-color: var(--header-background-color);
	}

  .div-nav-item-links {
    width: 90px;
    margin: 30px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .links-img-div {
    cursor: default;
  }

	
  .links-img-div:hover {
    background-color: var(--twitter-discord-background-color);
  }
  

  .links {
    display: none;
  }

  .mobile-menu {
    display: block;
    cursor: default;
    z-index: 4;
  }
}

@media (max-width: 1235px) and (max-height: 630px) {

  .nav {
    height: 365px;
    top: -365px;
  }

  .nav-item, .nav-item:hover, .nav-item-placeholder {
    font-size: 0.8rem;
  }

  .div-nav-item-links {
    margin: 15px 0;
  }
}

