/* Footer */

.footer {
  width: 100%;
  height: 250px;
  background-color: var(--header-background-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-twitter-discord-div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.footer-twitter-discord-tag {
  padding-left: 20px;
  font-size: 1rem;
  color: white;
}

.footer-text {
  width: 90%;
  padding-bottom: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  text-align: center;
} 

.footer-logos-div {
  height: 100px;
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 750px) {

  .footer-twitter-discord-div {
    cursor: default;
  }
}
